import { Link } from "react-router-dom";
import styled from "styled-components";

/*
       
  hover     
  active   
*/
const SideItem = styled.li`
  font-size: 1rem;
  padding: 5px;
  margin-left: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;

  &.active,
  &:hover {
    color: black;
    background-color: #76c9e8;
  }
`;

const SideLink = styled(Link)`
  width: 100%;
  text-decoration: none;
  color: white
`;

function SideBarItem({ icon, title, link, clicked, onClick }) {
  return <SideItem
    className={clicked === title ? "active" : null} //    
    //   
    onClick={onClick}> 
    {/* 
     -props   
     -link : route path 
    */}
    <SideLink to={link}>
      {icon}
      {title}
    </SideLink>
  </SideItem>
}

export default SideBarItem;