import { useState } from "react";
import styled from "styled-components";

const ImageContainer = styled.div`
  width: 80%;
  height: 200px;
  margin-bottom: 10px;
  border: 1px solid lightGray;
  border-radius: 10px;

  .image-input{
    display:none;
  }
`;

const ImageLabel = styled.label`
  margin: 5px 5px;
  padding: 3px;
  border: 1px solid #7ca2eb;
  border-radius: 10px;
  font-weight: bold;
  font-size: 10px;
  color: #7ca2eb;
  cursor: pointer;
`;

const AddedImage = styled.div`
  width: 100px;
  height: 100px;
  margin: 10px 5px;
  position: relative;

  .add-image{
    border-radius: 10px;
    width: 100%;
    height: 100%;
  }

  .delete{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #7ca2eb;
    padding: 1px 5px;
    font-weight: bold;
    font-size: 1rem;
    top: -10px;
    left: 90px;
    background: white;
    color: #7ca2eb;
    cursor: pointer;
  }
`;

/*
  -     
  -  3 
  -    
*/
function Images(props) {
  //  state
  const [showImages, setShowImages] = useState([]);
  let imageLists = [];

  //   
  const addImages = (e) => {
    //  
    const files = e.target.files;

    // URL  
    let imageUrlLists = [...showImages];
    for (let i = 0; i < files.length; i++) {
      //    URL 
      const currentImageUrl = URL.createObjectURL(files[i]);
      imageUrlLists.push(currentImageUrl); // URL 

      //    
      imageLists.push(files[i]);
    }
    //     
    if (imageUrlLists.length > 3) {
      //      
      imageUrlLists = imageUrlLists.slice(0, 3);
      imageLists = imageLists.slice(0, 3);
      alert("  3 .");
    }
    //state 
    props.setFiles(imageLists);
    setShowImages(imageUrlLists);
  }

  /*
    -  x         
    -props.setFiles() :  files  filter  
    -showImages     
  */
  const deleteImage = (id) => {
    props.setFiles((current) => current.filter((_, index) => index !== id));
    setShowImages(showImages.filter((_, index) => index !== id));
  }
  return (
    <ImageContainer>
      <div>
        <ImageLabel htmlFor="inputFile" onChange={addImages}>
          <input className="image-input" type="file" id="inputFile" multiple />
          <span style={{fontSize: "0.7rem"}}> </span>
        </ImageLabel>
      </div>
      <div style={{ display: "flex" }}>
        {showImages.map((image, id) => {
          return (
            <AddedImage key={id}>
              <span className="delete" onClick={() => deleteImage(id)}>X</span>
              <img className="add-image" src={image} alt={`${image}-${id}`} />
            </AddedImage>
          )
        })}
      </div>
    </ImageContainer>
  );
}

export default Images;