import { useState } from "react";
import styled from "styled-components";

const ImageContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 200px;
  margin-bottom: 10px;

  .image-input{
    display:none;
  }
`;

const ImageLabel = styled.label`
  margin: 5px 5px;
  padding: 3px;
  border: 1px solid #7ca2eb;
  border-radius: 10px;
  font-weight: bold;
  font-size: 10px;
  color: #7ca2eb;
  cursor: pointer;
`;

const AddedImage = styled.div`
  width: 100px;
  height: 100px;
  margin: 10px 5px;
  position: relative;

  .add-image{
    border-radius: 50%;
    width: 100%;
    height: 100%;
  }

  .delete{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #7ca2eb;
    padding: 1px 5px;
    font-weight: bold;
    font-size: 1rem;
    top: -10px;
    left: 90px;
    background: white;
    color: #7ca2eb;
    cursor: pointer;
  }
`;

/*
  -     
  -   1   
  -    
*/
function ProfileImage(props) {
  //  state
  const [showImage, setShowImage] = useState('');

  //   
  const addImage = (e) => {
    //  
    const file = e.target.files[0];

    //    URL 
    const currentImageUrl = URL.createObjectURL(file);

    //state 
    props.setFile(file);
    setShowImage(currentImageUrl);
  }

  /*
    -  x         
    -props.setFile() :  file  
    -showImage   
  */
  const deleteImage = () => {
    props.setFile("");
    setShowImage("");
  }
  return (
    <ImageContainer>
      <div>
        <ImageLabel htmlFor="inputFile" onChange={addImage}>
          <input className="image-input" type="file" id="inputFile" />
          <span style={{ fontSize: "0.7rem" }}> </span>
        </ImageLabel>
      </div>
      <div style={{ display: "flex" }}>
        {showImage !== "" ?
        <AddedImage>
          <span className="delete" onClick={deleteImage}>X</span>
          <img className="add-image" src={showImage} alt={`${showImage}`} />
        </AddedImage>
        : null}
      </div>
    </ImageContainer>
  );
}

export default ProfileImage;