import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import TopbarItem from "./TopbarItem";
//   import 
import TopbarMenu from "./TopbarMenu";

const ClientHeader = styled.header`
  position: sticky;
  top: 0;
  z-index: 999;
`;

// nav   div(  )
const TopbarDiv = styled.div`
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  background-color: white;

  box-shadow: 0px 0px 1px 0px #555;
`;

//   div
const TopbarWrapper = styled.div`
  width: 50%;
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

//Logo  span 
const Logo = styled.span`
  font-weight: bold;
  font-size: 30px;
  color: darkblue;
  cursor: pointer;
  .home-img{
    width: 100%;
    height: 50px;
  }
`;

//Nav  
const TopLeft = styled.div`
  display: flex;
  align-items: center;
`;
//Nav  
const TopCenter = styled.div`
  display: flex;
  align-items: center;
`;
//Nav  
const TopRight = styled.div`
  display: flex;
  align-items: center;
`;


function Topbar() {
  /*
    -location : route   url 
    -clicked : nav      state
    -Topbar    url  clicked 
    -link url   active  
  */
  const location = useLocation();
  //     url
  const navData = [
    { title: '', link: '/' },
    { title: '', link: '/shop' },
    { title: '', link: '/board' },
    { title: '', link: '/issue' },
    { title: '', link: '/my' }
  ];
  const [clicked, setClicked] = useState("");
  const [auth, setAuth] = useState(false);
  useEffect(() => {
    setClicked(location.pathname);
  }, [location]);
  useEffect(() => {
    const accessToken = localStorage.getItem("accessToken");
    if (accessToken !== null) {
      setAuth(true);
    } else {
      setAuth(false);
    }
  }, []);

  // 
  const handleLogout = () => {
    localStorage.clear();
    setAuth(false);

    alert(" .");
  }

  return (
    <ClientHeader>
      <TopbarDiv>
        <TopbarWrapper>
          <TopLeft>
            <Logo>
              <a href="/">
                <img  //Logo  
                  src="/img/home.png"
                  alt="..."
                  className="home-img"
                />
              </a>
            </Logo>
          </TopLeft>

          {/*      */}
          <TopCenter>
            <TopbarMenu
              navData={navData}
              clicked={clicked}
            />
          </TopCenter>

          <TopRight>
            {/*      active   */}
            {!auth ?
              <TopbarItem
                link={'/login'}
                title={'login'}
              /> :
              <TopbarItem
                link={'/'}
                title={'logout'}
                onClick={handleLogout}
              />
            }
          </TopRight>
        </TopbarWrapper>
      </TopbarDiv>
    </ClientHeader>
  );
}

export default Topbar;