import ClientContainer from "../../common/ClientContainer";
import Footer from "../main/Footer";
import Topbar from "../main/topbar/Topbar";
import ShopTitle from "./ShopTitle";
import ShopCategory from "./ShopCategory";
import { useEffect, useState } from "react";
import ShopList from "./ShopList";
import axios from "axios";
import { client } from "../../../App";

function ClientShop() {
  //  
  const [shops, setShops] = useState([]);
  //    state
  const [clicked, setClicked] = useState("");
  //button   
  const buttonData = [
    { title: "", value: "regdate" },
    { title: "", value: "shopScore" },
    { title: "", value: "status" },
  ];
  // 
  const [selectSpot, setSelectSpot] = useState({
    spotIdx: 0,
    localName: "",
    townName: "",
    spotName: "",
    spotLati: 0,
    spotLongi: 0,
  });

  //    
  const getShopList = () => {
    axios.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/shops`)
      .then((res) => {
        setShops(res.data);
        console.log(res.data);
      }).catch((err) => {
        alert(err.response.data.detail);
      })
  }
  
  //   
  useEffect(() => {
    getShopList();
    setClicked("regdate");
  }, []);

  //   
  const getShopListBySpot = async () => {
    const response = await client.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/shops/${selectSpot.spotName}`);
    console.log(response.data);
    setShops(response.data);
    if(response.data.length === 0){
      alert("    .");
    }
  }

  //    
  useEffect(() => {
    if(selectSpot.spotName !== ""){
      getShopListBySpot();
    }
  }, [selectSpot]);
  return (
    <>
      <Topbar />
      <ClientContainer>
        {/*   */}
        <ShopTitle
          selectSpot={selectSpot}
          setSelectSpot={setSelectSpot}
        />
        {/*   */}
        <ShopCategory
          data={buttonData}
          clicked={clicked}
          onClick={(e) => setClicked(e.target.value)}
        />
        {/*    */}
        {shops && shops.map((shop, index) => {
          return (
            <ShopList key={index} shop={shop} />
          )
        })}
      </ClientContainer>
      <Footer />
    </>
  )
}

export default ClientShop;