import { useEffect } from "react";
import { useState } from "react";
import styled from "styled-components";

const WidgetLgContainer = styled.div`
  flex: 2;
  border-radius: 10px;

  -webkit-box-shadow: 0px 0px 5px -1px #000000; 
  box-shadow: 0px 0px 5px -1px #000000;

  padding: 20px;
`;

const WidgetLgTitle = styled.h3`
  font-size: 1.3rem;
  font-weight: 800;
`;

const WidgetTableWrapper = styled.div`
  max-height: 200px;
  overflow: scroll;

  ::-webkit-scrollbar{
    display: none;
  }
`;

/*
  -table     
  -     
  -  max-height  overflow scroll
  -border-collapse :      
*/
const WidgetLgTable = styled.table`
  width: 100%;
  border-spacing: 20px;
  text-align: center;
  border-collapse: separate;
  border-spacing: 0px 10px;

  .widgetLgTh{
    position: sticky;
    top:0;
    background-color: white !important;
  }

  .widgetLgTime{
    font-size: 1.1rem;
    font-weight: 600;
    
    border-radius: 10%;
    background-color: darkblue;
    color: white;
  }
`;

/*
         
         
*/

function WidgetLg() {
  const dummyData = [
    { name: '1', time: 10, count: 4, phone: '01012345234', type: '' },
    { name: '2', time: 13, count: 2, phone: '01034532434', type: '' },
    { name: '3', time: 10, count: 5, phone: '01000100023', type: '' },
    { name: '4', time: 15, count: 1, phone: '01054744533', type: '' },
    { name: '5', time: 15, count: 6, phone: '01093458455', type: '' },
  ];
  //    data state
  const [data, setData] = useState([]);
  useEffect(() => {
    //      
    setData((data) => data = dummyData);
  }, []);
  return (
    <WidgetLgContainer>
      <WidgetLgTitle> </WidgetLgTitle>
      {/*   props     */}
      <WidgetTableWrapper>
        <WidgetLgTable>
          <thead  className="widgetLgTh">
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          {/*     */}
          <tbody>
            {data && data.map((data, index) => {
              return (
                <tr className="widgetLgTr" key={index}>
                  <td>{data.name}</td>
                  <td>
                    <span className="widgetLgTime">{data.time}</span>
                  </td>
                  <td>{data.count}</td>
                  <td>{data.phone}</td>
                  <td>{data.type}</td>
                </tr>
              )
            })}
          </tbody>
        </WidgetLgTable>
      </WidgetTableWrapper>
    </WidgetLgContainer>
  );
}

export default WidgetLg;