import styled from "styled-components";
import Input from "../../common/Input";
import Bt from "../../common/Bt";
import { useState } from "react";
import {client} from "../../../App";

const Container = styled.div`
  width: 100%;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  .wrapper {
    width: 100%;
    border-bottom: 1px solid #7ca5eb;
  }
  .input-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    .login-a{
      text-decoration: none;
      color: #7e8080;

      &:hover{
        color: #7ca2eb;
      }
    }
  }
  .evenly{
    margin: 1rem 0px;
    justify-content: space-evenly;
  }
`;

function LoginContainer() {
  const [memberId, setMemberId] = useState("");
  const [memberPass, setMemberPass] = useState("");

  const handleLogin = () => {
    if (memberId !== "" && memberPass !== "") {
      client.post(`${process.env.REACT_APP_REQUEST_URL}/api/client/login/member`, {
        memberId: memberId,
        memberPass: memberPass
      }).then((res) => {
        alert(" ");
        console.log(res.data);
        localStorage.setItem("accessToken", res.data);
        /*           
          -Authorization: 'Bearer ${accessToken}'  
          -      ,   
          -  axios  jwt  
        */

        window.location.href = "/";
      }).catch((err) => {
        alert(err.response.data.detail);
      });
    } else {
      alert("ID   .");
    }
  }
  return (
    <Container>
      <div className="wrapper">
        <div className="input-box">
          <Input
            type="text"
            placeholder="ID"
            value={memberId}
            onChange={(e) => setMemberId(e.target.value)}
            width="50%"
          />
        </div>
        <div className="input-box">
          <Input
            type="password"
            placeholder=" "
            value={memberPass}
            onChange={(e) => setMemberPass(e.target.value)}
            width="50%"
          />
        </div>
        <div className="input-box">
          <Bt
            btName=""
            width="30%"
            onClick={handleLogin}
          />
        </div>
        <div className="input-box evenly">
          <span>
            <a href="/join" className="login-a"></a>
          </span>
          <span>
            <a href="#" className="login-a">ID</a>
            /
            <a href="#" className="login-a"> </a>
          </span>
        </div>
      </div>
    </Container>
  )
}

export default LoginContainer;