import styled from "styled-components";
import SideBarMenu from "./SideBarMenu";
import HomeIcon from '@mui/icons-material/Home';
import ShowChartIcon from '@mui/icons-material/ShowChart';
import PersonIcon from '@mui/icons-material/Person';
import SurfingIcon from '@mui/icons-material/Surfing';
import LocalMallIcon from '@mui/icons-material/LocalMall';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import { useState } from "react";

/*
  TopBar    50px 
     
     sticky 
 */
const SideBarContainer = styled.div`
  flex: 1;
  height: calc(100vh - 50px);
  background-color: #001b33;

  -webkit-box-shadow: 0px 0px 5px 0px #000000; 
  box-shadow: 0px 0px 5px 0px #000000;

  position: sticky;
  top: 50px;
`;

/*
     div
    
*/
const SideBarWrapper = styled.div`
  padding: 20px;
  color: #white;

  .sideicon{
    margin-right: 5px;
    font-size: 2rem !important;
  }
`;


function Sidebar() {
  /*
    -    
    -title:   
    -sub :    
    -link :    path 
    -icon :     jsx
  */
  const menu = [
    {
      title: 'DashBoard',
      sub: ['', '', ''],
      link: ['/admin', '/admin/sales', '/admin/reservation'], //  to   
      icon: [
        <ShowChartIcon className="sideicon" />,
        <PersonIcon className="sideicon" />,
        <CalendarMonthIcon className="sideicon" />
      ]
    },
    {
      title: '',
      sub: ['', '', ''],
      link: ['/admin/shop', '/admin/trainer', '/admin/menu'], //  to   
      icon: [
        <HomeIcon className="sideicon" />,
        <SurfingIcon className="sideicon" />,
        <LocalMallIcon className="sideicon" />
      ]
    },
  ];
  //item  active   state
  const [clicked, setClicked] = useState("");
  const onClick = (sub) => {
    setClicked((current) => current = sub); // clicked state sub 
  };
  return (
    <SideBarContainer>
      <SideBarWrapper>
        {/*     */}
        {menu && menu.map((menu, index) => {
          return (
            <SideBarMenu
              onClick={onClick}
              clicked={clicked}
              menu={menu} //   props 
              key={index}
            />
          )
        })}
      </SideBarWrapper>
    </SideBarContainer>
  );
}

export default Sidebar;