import styled from "styled-components";
import ClientContainer from "../../common/ClientContainer";
import Footer from "../main/Footer";
import Topbar from "../main/topbar/Topbar";
import Input from "../../common/Input";
import DaumPost from "../../common/DaumPost";
import { useState } from "react";
import { useEffect } from "react";
import Bt from "../../common/Bt";
import { client } from "../../../App";

const InputBox = styled.div`
  width: 60%;
  margin: auto;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  
  .title{
    font-size: 2rem;
    font-weight: bold;
    border-bottom: 2px solid #7ca2eb;
  }
  &.center{
    justify-content: center;
  }
`;

const InfoLabel = styled.label`
  font-size: 0.8rem;
  color: ${props => props.color};
`;

const ImageLabel = styled.label`
  border-radius: 10px;
  padding: 10px;
  font-size: 1rem;
  background: #7ca2eb;
  color: white;
  cursor: pointer;
  
  > input {
    display: none;
  }
`;


function ClientJoin() {
  //        info  ---------------------------------------------
  const trueColor = "#7ca2eb";
  const falseColor = "#f0a779";

  //       ------------------------------------------------------------
  const [memberId, setMemberId] = useState("");
  const [memberPass, setMemberPass] = useState("");
  const [memberName, setMemberName] = useState("");
  const [image, setImage] = useState("");
  const [addressObj, setAddressObj] = useState({
    areaAddress: "",
    townAddress: "",
  });
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");

  /*     state-----------------------------------------------------------------
    -xxCheck :       
    -xxInfo :     
    -confirmPass :   state
  */
  const [idCheck, setIdCheck] = useState(""); //
  const [passCheck, setPassCheck] = useState(""); //
  const [phoneNoCheck, setPhoneNoCheck] = useState(false); //
  const [emailCheck, setEmailCheck] = useState(false); //
  const [phoneCheck, setPhoneCheck] = useState(false); //
  const [totalCheck, setTotalCheck] = useState(true); //    
  const [idInfo, setIdInfo] = useState("");
  const [passInfo, setPassInfo] = useState("");
  const [smsInfo, setSmsInfo] = useState("");
  const [confirmPass, setConfirmPass] = useState("");

  // ,  
  const [emailCode, setEmailCode] = useState("");
  const [smsCode, setSmsCode] = useState("");

  // ID    ------------------------------------------------------------------------
  const handleMemberId = (e) => {
    setMemberId(e.target.value);
    const idRegExp = /^[a-zA-z0-9]{4,12}$/;

    if (!idRegExp.test(memberId)) {
      setIdInfo("4-12    !");
      setIdCheck(false);
    } else {
      setIdInfo(" ID.");
      setIdCheck(true);
    }
  }

  //     -------------------------------------------------------------------
  const handleMemberPass = (e) => {
    const currentPass = e.target.value;
    const passRegExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,15}$/;

    if (!passRegExp.test(currentPass)) {
      setPassInfo("//  8~15  !");
      setPassCheck(false);
    } else {
      setPassInfo(" .");
      setPassCheck(true);
    }
    setMemberPass(currentPass);
  }

  //     -----------------------------------------------------------------------
  const handleConfirmPass = (e) => {
    const currentConfirm = e.target.value;

    if (memberPass !== currentConfirm) {
      setPassInfo("  !");
      setPassCheck(false);
    } else {
      console.log(currentConfirm);
      setPassInfo(" .");
      setPassCheck(true);
    }
    setConfirmPass(currentConfirm);
  }

  //      --------------------------------------------------------------------
  const handlePhone = (e) => {
    const currentPhone = e.target.value;
    const phoneRegExp = /^01([0|1|6|7|8|9])([0-9]{3,4})([0-9]{4})$/;

    if (!phoneRegExp.test(currentPhone)) {
      setSmsInfo("  !");
      setPhoneNoCheck(false);
    } else {
      setSmsInfo(" .");
      setPhoneNoCheck(true);
    }
    setPhone(currentPhone);
  };

  //    ------------------------------------------------------------------------------
  const sendEmail = () => {
    if (email === "") {
      alert(" .");
      return;
    }
    client.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/email/${email}/`)
      .then((res) => {
        alert(res.data.msg);
      }).catch((err) => {
        alert(err.response.data.detail);
      });
  }

  //   ---------------------------------------------------------------------------------
  const checkEmail = () => {
    if (emailCode === "") {
      alert(" .");
      return;
    }
    client.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/email-auth/${emailCode}`,
      { withCredentials: true })
      .then((res) => {
        alert(res.data.msg);
        if (res.data.code === 1) {
          setEmailCheck(true);
        }
      }).catch((err) => {
        alert(err.response);
      });
  }

  //   (     )
  // const sendSms = () => {
  //   if (phone === "") {
  //     alert("  .");
  //     return;
  //   }
  //   client.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/sms/${phone}`)
  //     .then((res) => {
  //       alert(res.data.msg);
  //       setSmsInfo("");
  //     })
  //     .catch((err) => {
  //       alert(err.response.data);
  //     });
  // }

  // //   
  // const checkSms = () => {
  //   if (smsCode === "") {
  //     alert(" .");
  //     return;
  //   }
  //   client.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/sms-auth/${smsCode}`)
  //     .then((res) => {
  //       alert(res.data.msg);
  //       if (res.data.code === 1) {
  //         setPhoneCheck(true);
  //       }
  //     })
  //     .catch((err) => {
  //       alert(err.response.data.detail);
  //     });
  // }

  // 
  const checkId = () => {
    if (memberId !== "") {
      client.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/${memberId}`)
        .then((response) => {
          setIdInfo(response.data.msg);
          setIdCheck(true);
        })
        .catch((err) => {
          setIdInfo(err.response.data.detail);
          setIdCheck(false);
          return;
        });
    }
  }

  //
  const registMember = async () => {
    let formData = new FormData();
    formData.append("memberId", memberId);
    formData.append("memberPass", memberPass);
    formData.append("memberName", memberName);
    formData.append("localAddress", addressObj.areaAddress);
    formData.append("detailAddress", addressObj.townAddress);
    formData.append("phoneNo", phone);
    formData.append("email", email);
    console.log(image);
    formData.append("image", image);


    // 
    await client.post(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/member`, formData, {
      //multipart    
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }).then((response) => {
      alert(response.data.msg);
      window.location.href = "/login";
    }).catch((error) => {
      alert(error.response.data.detail);
    }).finally(() => setImage(""));
  }

  //  true   
  useEffect(() => {
    if (idCheck && passCheck && emailCheck && phoneNoCheck) {
      setTotalCheck(false);
    }
  }, [idCheck, passCheck, emailCheck, phoneNoCheck]);
  return (
    <>
      <Topbar />
      <ClientContainer>
        <InputBox>
          <span className="title"></span>
        </InputBox>

        {/* ID   */}
        <InputBox>
          <Input
            placeholder="  ."
            value={memberId}
            onChange={handleMemberId}
            width="70%"
            mb="0px"
            onBlur={checkId}
          />
          <ImageLabel>
            
            <input
              type="file"
              onChange={(e) => setImage(e.target.files[0])}
            />
          </ImageLabel>
        </InputBox>
        <InputBox>
          <InfoLabel color={idCheck ? trueColor : falseColor}>
            {idInfo}
          </InfoLabel>
        </InputBox>
        {/* /.ID   */}

        {/*    */}
        <InputBox>
          <Input
            type="password"
            placeholder="  ."
            value={memberPass}
            onChange={handleMemberPass}
            width="70%"
            mb="0px"
          />
        </InputBox>
        <InputBox>
          <Input
            type="password"
            placeholder="   ."
            value={confirmPass}
            onChange={handleConfirmPass}
            width="70%"
            mb="0px"
          />
        </InputBox>
        <InputBox color="#7ca2eb">
          <InfoLabel color={passCheck ? trueColor : falseColor}>
            {passInfo}
          </InfoLabel>
        </InputBox>
        {/* /.   */}

        {/*    */}
        <InputBox>
          <Input
            type="text"
            placeholder=""
            value={addressObj.areaAddress}
            readOnly={true}
            name="localAddress"
            mb="0px"
          />
          <DaumPost
            setAddressObj={setAddressObj}
          />
        </InputBox>
        <InputBox>
          <Input
            type="text"
            placeholder=""
            value={addressObj.townAddress}
            name="detailAddress"
            readOnly={true}
          />
        </InputBox>
        {/* /.   */}

        {/*    */}
        <InputBox>
          <Input
            type="text"
            placeholder="email@example.com"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            mb="0px"
            dis={emailCheck}
          />
          <Bt
            btName=""
            onClick={sendEmail}
            dis={emailCheck}
            color={emailCheck ? "#7e8080" : trueColor}
          />
        </InputBox>
        <InputBox>
          <Input
            type="text"
            placeholder=" "
            value={emailCode}
            onChange={(e) => setEmailCode(e.target.value)}
            mb="0px"
            dis={emailCheck}
          />
          <Bt
            btName=""
            onClick={checkEmail}
            dis={emailCheck}
            color={emailCheck ? "#7e8080" : trueColor}
          />
        </InputBox>
        {/* /.   */}

        {/*    */}
        <InputBox>
          <Input
            type="text"
            placeholder=" ."
            value={memberName}
            onChange={(e) => setMemberName(e.target.value)}
            width="40%"
            mb="0px"
          />
        </InputBox>
        <InputBox>
          <Input
            type="text"
            placeholder="'-'  ."
            value={phone}
            onChange={handlePhone}
            mb="0px"
            dis={phoneCheck}
          />
          <Bt
            btName=""
            // onClick={sendSms}
            dis={true}
            // color={phoneCheck ? "#7e8080" : trueColor}
            color="#7e8080"
          />
        </InputBox>
        <InputBox>
          <InfoLabel color={phoneNoCheck ? trueColor : falseColor}>
            {smsInfo}
          </InfoLabel>
        </InputBox>
        <InputBox>
          <Input
            type="text"
            placeholder=" ."
            value={smsCode}
            onChange={(e) => setSmsCode(e.target.value)}
            mb="0px"
            dis={phoneCheck}
          />
          <Bt
            btName=""
            // onClick={checkSms}
            dis={true}
            // color={phoneCheck ? "#7e8080" : trueColor}
            color="#7e8080"
          />
        </InputBox>
        {/* /.   */}

        <InputBox className="center">
          <Bt
            btName=""
            onClick={registMember}
            dis={totalCheck}
            color={totalCheck ? "#7e8080" : trueColor}
          />
          <Bt
            btName=""
            onClick={() => window.history.back()}
            color={falseColor}
          />
        </InputBox>
      </ClientContainer>
      <Footer />
    </>
  )
}

export default ClientJoin;