import { addDays, endOfMonth, endOfWeek, format, isSameDay, isSameMonth, parseISO, startOfMonth, startOfWeek } from "date-fns";
import parse from "date-fns/parse";
import styled from "styled-components";

const CellContainer = styled.div`
  .flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .cell{
    width: 14%;
    text-align:center;
    font-weight: 600;
    font-size: 0.7rem;
    height: 70px;
    padding: 5px;
    border: 1px solid #cecece;
    border-radius: 10px;
    margin: 3px;
    cursor: pointer;
  }
  .valid{
    &:hover{
      transform: scale(1.1);
      border: none;
      background: #7ca2eb;
      color: white;
      opacity: 0.7;
    }
  }
  .not-valid{
    color: #cecece;
  }
  .selected{
    transform: scale(1.02);
    background: #7ca2eb;
    border: none;
    color: white;
    font-weight: 600;
  }
`;

function CalendarCells({ currentMonth, selectedDate, onDateClick }) {
  const monthStart = startOfMonth(currentMonth); // 
  const monthEnd = endOfMonth(monthStart); // 
  const startDate = startOfWeek(monthStart); // 
  const endDate = endOfWeek(monthEnd); // 

  const rows = []; //     
  let days = []; //   
  let day = startDate; // 
  let formattedDate = '';

  //     
  while (day <= endDate) {
    for (let i = 0; i < 7; i++) {
      formattedDate = format(day, 'd');
      const cloneDay = day;
      days.push(
        <div
          className={`cell ${!isSameMonth(day, monthStart) //    
              ? 'disabled'
              //      
              : isSameDay(day, selectedDate) || isSameDay(day, new Date())
                ? 'selected'
                : format(currentMonth, 'M') !== format(day, 'M')
                  ? 'not-valid'
                  : 'valid'
            }`}
          key={day}
          //  Cell  
          onClick={isSameMonth(day, monthStart) ? () => onDateClick(cloneDay) : null}
        >
          <span
            className={format(currentMonth, 'M') !== format(day, 'M')
              ? 'not-valid'
              : ''}
          >
            {formattedDate}
          </span>
        </div>,
      );
      day = addDays(day, 1);
    }
    //    1  
    rows.push(
      <div className="flex" key={day}>
        {days}
      </div>
    );
    days = []; //
  }
  return <CellContainer>{rows}</CellContainer>
}

export default CalendarCells;