import styled from "styled-components";
import SideBarItem from "./SideBarItem";

// 
const MenuContainer = styled.div`
  margin-bottom: 2rem;
`;

// 
const SideBarTitle = styled.h3`
  display: flex;
  margin: 0;
  align-items: center;
  font-size: 1.2rem;
  padding: 5px;
  color: white;
  border-bottom: 1px solid #76c9e8;
`;

//  
const SideBarList = styled.ul`
  margin: 0;
  list-style: none;
  padding: 0.5rem;
`;

function SideBarMenu({ menu, onClick, clicked }) {
  return (
    <MenuContainer>
      {/*  : sidebar  props  */}
      <SideBarTitle>
        {menu.title}
      </SideBarTitle>
      {/*  title home     */}
      <SideBarList>
        {menu.sub.map((sub, index) => {
          return (
            <SideBarItem
              key={index}
              icon={menu.icon[index]}
              title={sub}
              link={menu.link[index]}
              // clicked : Item     
              // onClick : sub(Item)   clicekd  
              clicked={clicked}
              onClick={() => onClick(sub)}
            />
          )
        })}
      </SideBarList>
    </MenuContainer>
  );
}

export default SideBarMenu;