import styled from "styled-components";
import ClientContainer from "../../common/ClientContainer";
import Footer from "../main/Footer";
import Topbar from "../main/topbar/Topbar";
import Input from "../../common/Input";
import DaumPost from "../../common/DaumPost";
import { useState } from "react";
import { useEffect } from "react";
import Bt from "../../common/Bt";
import {accessClient} from "../../../App";

const InputBox = styled.div`
  width: 60%;
  margin: auto;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  
  .title{
    font-size: 2rem;
    font-weight: bold;
    border-bottom: 2px solid #7ca2eb;
  }
  &.center{
    justify-content: center;
  }
`;

const InfoLabel = styled.label`
  font-size: 0.8rem;
  color: ${props => props.color};
`;

const ImageLabel = styled.label`
  border-radius: 10px;
  padding: 10px;
  font-size: 1rem;
  background: #7ca2eb;
  color: white;
  cursor: pointer;
  
  > input {
    display: none;
  }
`;


function MemberProfile() {
  //        info  ---------------------------------------------
  const trueColor = "#7ca2eb";
  const falseColor = "#f0a779";

  //       ------------------------------------------------------------
  const [memberId, setMemberId] = useState("");
  const [memberPass, setMemberPass] = useState("");
  const [memberName, setMemberName] = useState("");
  const [image, setImage] = useState("");
  const [addressObj, setAddressObj] = useState({
    areaAddress: "",
    townAddress: "",
  });
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");

  /*     state-----------------------------------------------------------------
    -xxCheck :       
    -xxInfo :     
    -confirmPass :   state
  */
  const [passCheck, setPassCheck] = useState(""); //
  const [phoneNoCheck, setPhoneNoCheck] = useState(false); //
  const [emailCheck, setEmailCheck] = useState(false); //
  const [phoneCheck, setPhoneCheck] = useState(false); //
  const [totalCheck, setTotalCheck] = useState(true); //    
  const [passInfo, setPassInfo] = useState("");
  const [smsInfo, setSmsInfo] = useState("");
  const [confirmPass, setConfirmPass] = useState("");

  // ,  
  const [emailCode, setEmailCode] = useState("");
  const [smsCode, setSmsCode] = useState("");

  //     -------------------------------------------------------------------
  const handleMemberPass = (e) => {
    const currentPass = e.target.value;
    const passRegExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,15}$/;

    if (!passRegExp.test(currentPass)) {
      setPassInfo("//  8~15  !");
      setPassCheck(false);
    } else {
      setPassInfo(" .");
      setPassCheck(true);
    }
    setMemberPass(currentPass);
  }

  //     -----------------------------------------------------------------------
  const handleConfirmPass = (e) => {
    const currentConfirm = e.target.value;

    if (memberPass !== currentConfirm) {
      setPassInfo("  !");
      setPassCheck(false);
    } else {
      console.log(currentConfirm);
      setPassInfo(" .");
      setPassCheck(true);
    }
    setConfirmPass(currentConfirm);
  }

  //      --------------------------------------------------------------------
  const handlePhone = (e) => {
    const currentPhone = e.target.value;
    const phoneRegExp = /^01([0|1|6|7|8|9])([0-9]{3,4})([0-9]{4})$/;

    if (!phoneRegExp.test(currentPhone)) {
      setSmsInfo("  !");
      setPhoneNoCheck(false);
    } else {
      setSmsInfo(" .");
      setPhoneNoCheck(true);
    }
    setPhone(currentPhone);
  };

  //    ------------------------------------------------------------------------------
  const sendEmail = async () => {
    if (email === "") {
      alert(" .");
      return;
    }
    const response = await accessClient.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/email${email}/`);
    alert(response.data.msg);
  }

  //   ---------------------------------------------------------------------------------
  const checkEmail = async () => {
    if (emailCode === "") {
      alert(" .");
      return;
    }

    const response = await accessClient.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/email-auth/${emailCode}`);
    alert(response.data.msg);
    if (response.data.code === 1) {
      setEmailCheck(true);
    }
  }

  //   
  const sendSms = async () => {
    if (phone === "") {
      alert("  .");
      return;
    }
    console.log(phone);
    const response = await accessClient.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/sms/${phone}`);
    alert(response.data.msg);
    setSmsInfo("");
  }

  //   
  const checkSms = async () => {
    if (smsCode === "") {
      alert(" .");
      return;
    }

    const response = await accessClient.get(`${process.env.REACT_APP_REQUEST_URL}/api/client/join/sms-auth/${smsCode}`);
    alert(response.data.msg);
    if (response.data.code === 1) {
      setPhoneCheck(true);
    }
  }

  // 
  const registMember = async () => {
    let formData = new FormData();

    //     
    if(memberPass != "") formData.append("memberPass", memberPass);
    if(memberName != "") formData.append("memberName", memberName);
    if(addressObj.areaAddress != "") formData.append("localAddress", addressObj.areaAddress);
    if(addressObj.townAddress != "") formData.append("detailAddress", addressObj.townAddress);
    if(phone != "") formData.append("phoneNo", phone);
    if(email != "") formData.append("email", email);
    if(image != "")formData.append("image", image);
    console.log(image);


    // 
    await accessClient.post(`${process.env.REACT_APP_REQUEST_URL}/api/client/token/member`, formData, {
      //multipart    
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }).then((response) => {
      alert(response.data.msg);
      window.location.href = "/my";
    }).catch((error) => {
      alert(error.response.data.detail);
    }).finally(() => setImage(""));
  }

  return (
    <>
      <Topbar />
      <ClientContainer>
        <InputBox>
          <span className="title"> </span>
        </InputBox>

        <InputBox>
          <Input
            value={memberId}
            width="70%"
            mb="0px"
            dis={true}
          />
          <ImageLabel>
            
            <input
              type="file"
              onChange={(e) => setImage(e.target.files[0])}
            />
          </ImageLabel>
        </InputBox>
        <InputBox>
          <InfoLabel color={falseColor}>
               .
          </InfoLabel>
        </InputBox>
        {/* /.ID   */}

        {/*    */}
        <InputBox>
          <Input
            type="password"
            placeholder="  ."
            value={memberPass}
            onChange={handleMemberPass}
            width="70%"
            mb="0px"
          />
        </InputBox>
        <InputBox>
          <Input
            type="password"
            placeholder="   ."
            value={confirmPass}
            onChange={handleConfirmPass}
            width="70%"
            mb="0px"
          />
        </InputBox>
        <InputBox color="#7ca2eb">
          <InfoLabel color={passCheck ? trueColor : falseColor}>
            {passInfo}
          </InfoLabel>
        </InputBox>
        {/* /.   */}

        {/*    */}
        <InputBox>
          <Input
            type="text"
            value={addressObj.areaAddress}
            readOnly={true}
            name="localAddress"
            mb="0px"
          />
          <DaumPost
            setAddressObj={setAddressObj}
          />
        </InputBox>
        <InputBox>
          <Input
            type="text"
            value={addressObj.townAddress}
            name="detailAddress"
            readOnly={true}
          />
        </InputBox>
        {/* /.   */}

        {/*    */}
        <InputBox>
          <Input
            type="text"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            mb="0px"
            dis={emailCheck}
          />
          <Bt
            btName=""
            onClick={sendEmail}
            dis={emailCheck}
            color={emailCheck ? "#7e8080" : trueColor}
          />
        </InputBox>
        <InputBox>
          <Input
            type="text"
            placeholder=" "
            value={emailCode}
            onChange={(e) => setEmailCode(e.target.value)}
            mb="0px"
            dis={emailCheck}
          />
          <Bt
            btName=""
            onClick={checkEmail}
            dis={emailCheck}
            color={emailCheck ? "#7e8080" : trueColor}
          />
        </InputBox>
        {/* /.   */}

        {/*    */}
        <InputBox>
          <Input
            type="text"
            placeholder=" ."
            value={memberName}
            onChange={(e) => setMemberName(e.target.value)}
            width="40%"
            mb="0px"
          />
        </InputBox>
        <InputBox>
          <Input
            type="text"
            placeholder="'-'  ."
            value={phone}
            onChange={handlePhone}
            mb="0px"
            dis={phoneCheck}
          />
          <Bt
            btName=""
            onClick={sendSms}
            dis={phoneCheck}
            color={phoneCheck ? "#7e8080" : trueColor}
          />
        </InputBox>
        <InputBox>
          <InfoLabel color={phoneNoCheck ? trueColor : falseColor}>
            {smsInfo}
          </InfoLabel>
        </InputBox>
        <InputBox>
          <Input
            type="text"
            placeholder=" ."
            value={smsCode}
            onChange={(e) => setSmsCode(e.target.value)}
            mb="0px"
            dis={phoneCheck}
          />
          <Bt
            btName=""
            onClick={checkSms}
            dis={phoneCheck}
            color={phoneCheck ? "#7e8080" : trueColor}
          />
        </InputBox>
        {/* /.   */}

        <InputBox className="center">
          <Bt
            btName=""
            onClick={registMember}
            color={trueColor}
          />
          <Bt
            btName=""
            onClick={() => window.history.back()}
            color={falseColor}
          />
        </InputBox>
      </ClientContainer>
      <Footer />
    </>
  )
}

export default MemberProfile;