import styled from "styled-components";
import TopbarItem from "./TopbarItem";

// 
const MenuContainer = styled.div`
  display: flex;
`;

//  
const TopbarList = styled.ul`
  display: flex;
  margin: 0;
  list-style: none;
  padding: 0.5rem;
`;

function TopbarMenu({ navData, clicked }) {
  return (
    <MenuContainer>
      <TopbarList>
        {/* // clicked : Item     
        // onClick : sub(Item)   clicekd  
        // clicked={clicked} */}
        {navData && navData.map((data, index) => {
          return (
            <TopbarItem
              key={index}
              title={data.title}
              link={data.link}
              clicked={clicked}
            />
          )
        })}
      </TopbarList>
    </MenuContainer>
  );
}

export default TopbarMenu;