import styled from "styled-components";
import SideBarItem from "./BottomItem";

// 
const MenuContainer = styled.div`
  margin: 10px 0px;

  .title{
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    font-weight: bold;
    color: #7e8080;
  }
`;

//  
const SideBarList = styled.ul`
  margin: 0;
  list-style: none;
  padding: 0.5rem;
`;

function SideBarMenu({ menu, adminPage }) {
  return (
    <MenuContainer>
      {/*  : sidebar  props  */}
      <label className="title">
        {menu.title}
      </label>
      {/*  title home     */}
      <SideBarList>
        {menu.sub.map((sub, index) => {
          //        
          return (sub === '' && !adminPage ?
            null :
            <SideBarItem
              key={index}
              title={sub}
              link={menu.link[index]}
            />
          )
        })}
      </SideBarList>
    </MenuContainer>
  );
}

export default SideBarMenu;