import { useState } from "react";
import styled from "styled-components";
import Bt from "../../common/Bt";
import Input from "../../common/Input";
import ProfileImage from "../../common/ProfileImage";
import axios from "axios";

const InputContainer = styled.div`
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 15px;
  text-align: 

  -webkit-box-shadow: 0px 0px 5px -1px #000000; 
  box-shadow: 0px 0px 5px -1px #000000;
`;

function MenuForm({ shopIdx, setRegist }) {
  //Input value state
  const [menuName, setMenuName] = useState("");
  const [menuPrice, setMenuPrice] = useState("");
  const [menuDesc, setMenuDesc] = useState("");
  //  
  const [file, setFile] = useState([]);

  //   
  const registMenu = () => {
    if (window.confirm("?")) {
      let formData = new FormData();

      formData.append("shopIdx", shopIdx);
      formData.append("menuName", menuName);
      formData.append("menuPrice", menuPrice);
      formData.append("menuDesc", menuDesc);
      console.log(file);
      formData.append("image", file);

      // 
      axios.post(`${process.env.REACT_APP_REQUEST_URL}/api/menu`, formData, {
        //multipart    
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response) => {
        alert(response.data.msg);
        setRegist(false);
      }).catch((error) => {
        alert(error.response.data.detail);
      }).finally(() => setFile(""));
    }
  }
  return (
    <InputContainer>
      <ProfileImage setFile={setFile} />
      <Input
        type="text"
        placeholder=" "
        name="menuName"
        value={menuName}
        onChange={(e) => setMenuName(e.target.value)}
      />
      <Input
        type="text"
        placeholder=" (ex: 2 = 20000)"
        name="menuPrice"
        value={menuPrice}
        onChange={(e) => setMenuPrice(e.target.value)}
      />
      <Input
        type="text"
        placeholder=" (50  .)"
        name="menuDesc"
        value={menuDesc}
        onChange={(e) => setMenuDesc(e.target.value)}
      />
      <Bt type="button" btName="" onClick={registMenu} />
    </InputContainer>
  );
}

export default MenuForm;