import styled from "styled-components";
import TrendingUpIcon from '@mui/icons-material/TrendingUp';
import TrendingDownIcon from '@mui/icons-material/TrendingDown';

//    
const InfoContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
`;

//    div
const InfoItem = styled.div`
  flex: 1;
  margin: 0px 20px;
  padding: 20px 30px;
  border-radius: 10px;
  cursor: pointer;

  -webkit-box-shadow: 0px 0px 5px -1px #000000; 
  box-shadow: 0px 0px 5px -1px #000000;
`;

//  
const InfoTitle = styled.span`
  font-size: 1.3rem;
  font-weight: 800;
  padding-bottom: 5px;
  border-bottom: 1px solid #555;
`;

//  
const InfoResultContainer = styled.div`
  margin: 10px 0px;
  display: flex;
  align-items: center;
`;

// 
const InfoMoney = styled.span`
  font-size: 2rem;
  font-weight: 600;
`;

//
const InfoRate = styled.span`
  display: flex;
  align-items: center;
  margin-left: 20px;

  .infoIcon{
    font-size: 1.3rem;
    margin-left: 5px;
    color: blue;
  }

  .infoIcon.negative{
    color: red;
  }
`;

// 
const InfoSub = styled.span`
  font-size: 0.9rem;
  color: #555;
`;

function Info() {
  const infos = [
    { title: ' ', money: 2500, rate: -10.3 },
    { title: '', money: 1100, rate: -20.2 },
    { title: '', money: 1400, rate: +9.9 },
  ];
  return (
    <InfoContainer>
      {/* item   */}
      {infos.map((info, index) => {
        return (
          <InfoItem key={index}>
            <InfoTitle>{info.title}</InfoTitle>
            <InfoResultContainer>
              <InfoMoney>{info.money}</InfoMoney>
              <InfoRate>
                Trend: {info.rate}
                {info.rate < 0 ?
                  <TrendingDownIcon className="infoIcon negative" /> :
                  <TrendingUpIcon className="infoIcon" />
                }
              </InfoRate>
            </InfoResultContainer>
            <InfoSub>  </InfoSub>
          </InfoItem>
        )
      })}
      {/*   */}
    </InfoContainer>
  );
}

export default Info;